:root{
    --gradient: linear-gradient(38deg, rgba(83,8,130,1) 0%, rgba(27,195,136,1) 100%);
}


body {
    /* background: rgb(155,89,182);
    background: linear-gradient(50deg, rgba(155,89,182,1) 1%, rgba(41,128,185,1) 100%); */
    background-image: linear-gradient(38deg, rgba(83,8,130,1) 0%, rgba(27,195,136,1) 100%);
    background-image: var(--gradient);
    background-size: 150%;
    height: 100vh;
    width: 100vw;
    -webkit-animation: bg-animation 15s infinite alternate;
            animation: bg-animation 15s infinite alternate;
}

@-webkit-keyframes bg-animation{
    0%{
        background-position: left;
    }
    100%{
        background-position: right;
    }
}

@keyframes bg-animation{
    0%{
        background-position: left;
    }
    100%{
        background-position: right;
    }
}
    
